body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100vw;height:100vh;display:grid;grid-template-areas:"header header" "main main" "footer footer";grid-template-rows:auto 1fr auto;background-color:#f0efef}.App .Header{grid-area:header}.App .App-main-content{grid-area:main;height:100%;overflow:hidden}.App>.Footer{grid-area:footer}

.Footer{background-color:white;text-align:center}

.Header{display:flex;padding:10px 15vw;background-color:black}.Header h1{color:white;margin:0;margin-right:30px}.Header a{display:block;margin:0 5px;padding:10px 20px;min-width:70px;text-align:center;font-size:1.2em;text-decoration:none;border:2px solid black;color:#999;-webkit-transition:0.1s ease-out all;transition:0.1s ease-out all}.Header a:hover{border:2px solid #333}.Header a.active{border:2px solid white;color:white}

.QuizOptionComponent{margin:10px 0;padding:5px 20px;background-color:#ffff}.QuizOptionComponent.wrong{background-color:#f12c2c}.QuizOptionComponent.correct{background-color:#73f573}

.QuizQuestionPanel{display:grid;grid-template-areas:"q q" "o o" "s n";grid-template-rows:auto 1fr auto;grid-template-columns:1fr auto;height:100%}.QuizQuestionPanel .QuizQuestionPanel-question{grid-area:q;display:flex;align-items:center;font-size:1.5em}.QuizQuestionPanel .QuizQuestionPanel-options{grid-area:o;flex:1 1}.QuizQuestionPanel .QuizQuestionPanel-next-button{grid-area:n;margin-bottom:30px;padding:10px 40px;background-color:#3874e4;color:white;border:none}.QuizQuestionPanel .QuizQuestionPanel-score{display:flex;justify-content:center;align-items:center;grid-area:s;height:auto;margin-bottom:30px;padding:5px;margin-right:10px;color:black;border:1px solid black;font-size:1em}

.AnalysisQuiz{height:100%;padding:0 15vw}

.TheoremPanel{padding:20px;margin:10px 0;box-shadow:-1px 7px 20px 0px rgba(0,0,0,0.1);background-color:white;color:black}.TheoremPanel .TheoremPanel-name{font-size:1.3em;text-align:center;color:black}.TheoremPanel .TheoremPanel-prereqs{margin-bottom:50px}

.TheoremList{box-sizing:border-box;height:100%;display:flex;flex-direction:column;align-items:center;padding:100px 50px;overflow-y:scroll}.TheoremList .TheoremList-list{display:flex;flex-direction:column}

.AnalysisHome{display:flex;flex-direction:column;height:100%}.AnalysisHome .AnalysisHome-subpage-links{display:flex;flex-direction:column;align-items:center;margin:20px 0}.AnalysisHome .AnalysisHome-subpage-links .SectionLink{margin:10px 0;min-width:300px}

.SectionLink{padding:10px 20px;border:1px solid black;text-align:center;color:black;text-decoration:none}

.HomePage{height:100%;display:flex;flex-direction:column;align-items:center;background-color:#d80971}.HomePage .HomePage-title{display:block;margin-bottom:0.1em;font-size:10vh;text-align:center;color:white}.HomePage .HomePage-subpages{display:flex}.HomePage .HomePage-subpages a{display:flex;justify-content:center;align-items:center;width:150px;height:150px;margin:10px;font-size:1.5em;text-decoration:none;color:white;border:1px solid white}

